export default {
  data () {
    return {
      pageShow: false,
      info: {}
    }
  },

  onLoad () {
    this.getInfo()
  },

  methods: {
    getInfo () {
      this.$loading()
      this.$http.getInviteInfo({
        pin_id: this.$mp.query.pin_id,
        tuan_id: this.$mp.query.tuan_id
      }).then(res => {
        if (res.status === 200) {
          this.info = res.data
          this.pageShow = true
        } else {
          this.$toast(res.mess)
        }
      })
    },

    invite () {
      uni.share({
        type: 0,
        provider: 'weixin',
        scene: 'WXSceneSession',
        title: 'wosmart 商城',
        summary: this.info.goods_name,
        href: `${this.info.weburl}?pin_id=${this.$mp.query.pin_id}&tuan_id=${this.$mp.query.tuan_id}&goods_id=${this.info.goods_id}`,
        imageUrl: `https://www.bmsapp.cn/uploads/logo.png`,
        success: () => {
          this.$toast('分享成功')
        },
        fail: (err) => {
          this.$toast('分享失败')
        }
      })
    },

    copy () {
      uni.setClipboardData({
        data: `${this.info.weburl}?pin_id=${this.$mp.query.pin_id}&tuan_id=${this.$mp.query.tuan_id}&goods_id=${this.info.goods_id}`,
        success: () => {
          this.$toast('复制成功')
        }
      })
    }
  }
}
